.btn {
    @apply flex bg-gray-500 rounded-lg px-4 py-1 text-white text-base font-medium shadow-md items-center;

    @screen md {
        @apply px-6 text-lg;
    }
    @screen xl {
        @apply py-2 text-base;
    }

    svg {
        @apply inline-flex;
    }
    span {
        vertical-align: middle;
    }

    &:hover {
        @apply bg-gray-600;
    }
}

.btn-primary {
    @apply bg-primary border-2 border-primary-darker text-white shadow-md;

    &:hover {
        @apply bg-primary-lighter border-primary text-white shadow-lg;
    }
}

.btn-secondary {
    @apply bg-white border-2 border-primary-lighter text-black shadow-md;

    &:hover {
        @apply bg-white border-primary-darker shadow-lg;
    }
}

.btn-size-lg {
    @apply px-10 py-4 text-lg;
}

.btn-size-sm {
    @apply py-2 px-2 text-sm;
}

.action {
    --tw-bg-opacity: 1;
    --tw-text-opacity: 1;
    --tw-shadow: 0 4px 6px -1px rgba(0, 0, 0, .1), 0 2px 4px -2px rgba(0, 0, 0, .1);
    align-items: center;
    border-radius: 50px;
    display: flex;
    font-size: 1rem;
    font-weight: 500;
    line-height: 1.5rem;
    padding: .25rem 1rem
}

.action.primary {
    --tw-border-opacity: 1;
    --tw-bg-opacity: 1;
    --tw-text-opacity: 1;
    --tw-shadow: 0 4px 6px -1px rgba(0, 0, 0, .1), 0 2px 4px -2px rgba(0, 0, 0, .1);
    border-width: 2px;
    background-color: #2C2F74;
    border-color: #2C2F74;
    color: #fff;
}

.action.primary:hover {
    background-color: #fff;
    color: #2C2F74;
}

.action.secondary {
    --tw-border-opacity: 1;
    --tw-bg-opacity: 1;
    --tw-text-opacity: 1;
    --tw-shadow: 0 4px 6px -1px rgba(0, 0, 0, .1), 0 2px 4px -2px rgba(0, 0, 0, .1);
    border-width: 2px;
    border-color: #2C2F74;
    background-color: #fff;
    color: #2C2F74;
}

.action.secondary:hover {
    background-color: #2C2F74;
    color: #fff;
}

.place-order {
    width: 100%;
    justify-content: center;
    padding: 7px 1rem;
}
