body {
    @apply bg-container;
}

#cart-drawer {
    @apply text-black;
}

[x-cloak] {
    display: none;
}

.text-indigo {
    color: #2C2F74;
    font-size: 24px;
    line-height: 24px;
    font-weight: 500;
    font-family: auto, sans-serif;
}

.hidden.block {
    display: block !important;
}

.w-image {
    width: 20%;
    max-width: 100px;
}

.w-image img {
    margin: 0 auto;
    border: 2px solid #D9D9D9;
    border-radius: 10px;
    padding: 10px;
}

.w-info {
    width: 65%;
    max-width: 340px;
}

.flex .container {
    max-width: 1440px;
}

.container .w-full.lg\:w-3\/5 {
    max-width: 780px;
}

.container .w-full.lg\:w-2\/5 {
    max-width: 560px;
}

.items-center .form-control {
    margin-right: 15px;
    margin-top: 0 !important;
}

.sku {
    margin-top: 15px;
}

.bottom-custom {
    margin-top: 15px;
}

.option-name {
    margin-top: 15px;
}

.name, .sku, .option-name {
    font-size: 16px;
    line-height: 22px;
    color: #666666;
}

body.bg-container {
    background-color: #fff !important;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.qty .form-input {
    text-align: center;
    width: 100px !important;
}

.qty .decrement, .qty .increment {
    position: absolute;
    color: #B2B2B2;
    font-size: 25px;
}

.qty .decrement {
    left: 10px;
}

.qty .increment {
    right: 10px;
}

.page-title {
    text-align: center;
    font-size: 36px;
    line-height: 36px;
    font-weight: 500;
    color: #2C2F74;
    margin: 50px 0;
}

.input {
    @apply w-auto rounded mr-2 border text-base py-2 px-4;

    @screen md {
        @apply mr-4;
    }

    @screen lg {
        @apply mr-0;
    }

    @screen xl {
        @apply mr-4;
    }

    &-light {
        @apply bg-white border-gray-400;

        &:focus {
            @apply outline-none border-primary;
        }
    }
}

.card {
    @apply p-4 rounded-sm bg-container-lighter shadow;
}

.card-interactive {
    &:hover {
        @apply bg-container;
    }
}
